.article {
	@include f(17px,400,1.59);
	@include sm {
		margin-bottom: 20px;
		font-size: 14px;
		line-height: 1.4; }
	&:not(:last-child) {
		margin-bottom: 32px;
		@include sm {
			margin-bottom: 20px; } }
	&:last-child {
		.col:last-child & {
			@include sm {
				margin-bottom: 0; } } }
	&_sm {
		@include f(14px,400,1.428); }
	&__img {
		font-size: 0;
		img {
			width: 100%;
			display: block; } }
	&__wrap {
		padding: 40px 32px 32px;
		@include sm {
			padding: 16px 0; }
		.article_sm & {
			padding: 16px 15px 20px;
			@include sm {
				padding: 16px 0; } } }
	&__head {
		width: 100%;
		display: table;
		@include md {
			display: block; }
		&:not(:last-child) {
			margin-bottom: 25px;
			@include sm {
				margin-bottom: 15px; } } }
	&__tags {
		margin-bottom: 4px; }
	&__title-md {
		display: block;
		transition: color .3s;
		@include hover {
			color: $lightBrown; }
		.title {
			@include hover {
				color: $lightBrown; } }
		&:not(:last-child) {
			margin-bottom: 10px; } }
	&__title {
		margin-bottom: 0;
		transition: color .3s;
		vertical-align: middle;
		display: table-cell;
		@include md {
			margin-bottom: 15px;
			display: block; }
		@include hover {
			color: $lightBrown; }
		.title {
			transition: color .3s;
			@include hover {
				color: $lightBrown; } } }
	&__button {
		text-align: right;
		vertical-align: top;
		display: table-cell;
		@include md {
			text-align: left;
			display: block; } }
	&__text {
		&:not(:last-child) {
			margin-bottom: 25px;
			@include sm {
				margin-bottom: 15px; } } } }

