.product {
	position: relative;
	z-index: 1;
	color: $black;
	background-color: $white;
	@include md {
		padding: 16px; }
	&:hover {
		&:before {
			opacity: 1; }
		.product__footer {
			opacity: 1;
			visibility: visible; } }
	&:before {
		position: absolute;
		content: "";
		top: -16px;
		left: -16px;
		right: -16px;
		bottom: -16px;
		z-index: -1;
		opacity: 0;
		transition: opacity .3s ease-in-out;
		box-shadow: 0 6px 12px rgba(#000, .16);
		background-color: $white;
		@include md {
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			visibility: visible;
			opacity: 1; } }
	&__img {
		font-size: 0;
		img {
			width: 100%;
			height: 224px;
			object-fit: cover;
			display: block; } }
	&__wrap {
		padding: 12px 0 0; }
	&__name {
		@include f(14px, 400, 1.285);
		&:not(:last-child) {
			margin-bottom: 12px; } }
	&__price {
		@include f(16px, 400, 1.375); }
	&__footer {
		margin-top: 50px;
		opacity: 0;
		visibility: hidden;
		transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
		@include clr;
		@include md {
			margin-top: 30px;
			visibility: visible;
			opacity: 1; }
		@include sm {
			margin-top: 20px; } }
	&__btn {
		float: left;
		width: calc(100% - 56px); }
	&__like {
		float: right; } }
